:root {
  --lime-green: hsl(163, 72%, 41%);
  --bright-red: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-light: hsl(230, 19%, 60%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --toggle-bg-dark: linear-gradient(225deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
  --toggle-button-light: hsl(228, 46%, 96%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-text1: hsl(230, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --background: var(--light-bg);
  --text-color1: var(--light-text1);
  --text-color2: var(--light-text2);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg);
    --text-color1: var(--dark-text1);
    --text-color2: var(--dark-text2);
    --card-bg: var(--dark-card);
    --card-hover: var(--dark-card-hover);
    --toggle: var(--light-bg);
    --toggle-bg: var(--toggle-bg-dark);
    --toggle-button: var(--dark-bg);
  }
}
body.light {
  --background: var(--light-bg);
  --text-color1: var(--light-text1);
  --text-color2: var(--light-text2);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

body.dark {
  --background: var(--dark-bg);
  --text-color1: var(--dark-text1);
  --text-color2: var(--dark-text2);
  --card-bg: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --toggle: var(--light-bg);
  --toggle-bg: var(--toggle-bg-dark);
  --toggle-button: var(--dark-bg);
}

:root {
  --font-inter: "Inter", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--background);
  color: var(--text-color2);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  margin-bottom: 0.1875rem;
  font-size: 1.5rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}

a {
  display: inline-flex;
}

a, a::visited, a::active {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  margin: 0 auto 2.875rem;
  padding: 0 1.5625rem;
  max-width: 68.75rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.cards-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.header {
  padding-top: 2.25rem;
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
  width: auto;
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color1);
}

.toggle {
  margin: 0;
  display: grid;
  grid-template-columns: 1fr 3rem;
  border: none;
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--toggle);
}
.toggle label[for=dark] {
  margin-right: 0.8125rem;
  line-height: 1.5rem;
}
.toggle__wrapper {
  position: relative;
  display: flex;
  height: 1.5rem;
}
.toggle input[type=radio] {
  margin: 0;
  width: 1.5rem;
  height: 1.5rem;
  opacity: 0;
  cursor: pointer;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 0.125rem solid white;
}
.toggle__background {
  display: block;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 0.75rem;
  background: var(--toggle-bg);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  top: 0.1875rem;
  left: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle-button);
  transition: all 0.1s ease-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translateX(-50%);
}

.card {
  position: relative;
  overflow: hidden;
  padding: 1.5625rem;
  height: 13.5rem;
  border-radius: 0.3125rem;
  background-color: var(--card-bg);
  color: var(--text-color1);
  text-align: center;
  cursor: pointer;
  transition: background-color 0.15s ease-in-out;
}
.card:hover {
  background-color: var(--card-hover);
}
.card__platform {
  margin-top: 0.25rem;
  margin-bottom: 1.75rem;
  height: 1.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card--fb {
  border-top: 0.25rem solid var(--facebook);
}
.card--tw {
  border-top: 0.25rem solid var(--twitter);
}
.card--ig {
  padding-top: 1.875rem;
}
.card--ig::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 0.25rem;
  width: 100%;
  background: linear-gradient(225deg, var(--instagram-end), var(--instagram-middle), var(--instagram-start));
}
.card--yt {
  border-top: 0.25rem solid var(--youtube);
}
.card__icon {
  margin-right: 0.5rem;
  display: inline-flex;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.5625rem;
  display: flex;
  flex-direction: column;
}
.card__count {
  margin-bottom: 0.25rem;
  font-weight: 700;
  letter-spacing: -0.125rem;
  color: var(--text-color2);
  line-height: 1;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  font-size: 0.75rem;
  font-weight: 400;
  letter-spacing: 0.3125rem;
  color: var(--text-color1);
  text-transform: uppercase;
}
.card__change {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--lime-green);
}
.card__change--down {
  color: var(--bright-red);
}
.card__change img {
  margin-right: 0.25rem;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  height: auto;
  justify-items: start;
  gap: 1.4375rem;
}
.card-grid .card__count {
  margin: 0;
}
.card-grid .card__count, .card-grid .card__change {
  align-self: end;
}
.card-grid .card__icon, .card-grid .card__change {
  margin: 0;
  justify-self: end;
}